$main-font: 'Roboto Condensed', sans-serif;
$sec-font: 'Fira Sans Extra Condensed', sans-serif;
$black: #2c2c2c;
$blue: #2e3841;
$orange: #fdc236;
$white: #f9fafb;
$fb-color: #3b5998;
$trans: all 0.2s ease-out;

*,:before,:after {
  box-sizing: border-box; }
html {
  -webkit-tap-highlight-color: transparent; }
body {
  font-family: $main-font;
  font-size: 16px;
  line-height: 1.3;
  color: $black; }
.container {
  width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
  @media (max-width:1220px) {
    width: 100%; } }
.nav {
  display: flex;
  list-style: none;
  padding: 0;
  margin: 0; }
p {
  margin: 0; }
a {
  text-decoration: none;
  color: inherit;
  outline: none; }
ul {
  padding: 0;
  list-style-type: none; }
figure {
  margin: 0; }

@import "assets/css/header";
